/* eslint-disable react/react-in-jsx-scope */
import { Route, Switch } from 'react-router-dom';
import {MainPageContainer} from '../components/pages/MainPage/MainPage';
import OrderPage from '../components/pages/OrderPage/OrderPage';
import StepOne from '../components/pages/OrderPage/StepOne/StepOne';
import StepTwo from '../components/pages/OrderPage/StepTwo/StepTwo';
import StepThree from '../components/pages/OrderPage/StepThree/StepThree';

export const Routes = () => {
    return (
        <>
        <Switch>
        <Route exact path="/need-for-drive" component={MainPageContainer} />
        <Route>
            <OrderPage>
            <Switch>
            <Route path="/need-for-drive/order-page/1" component={StepOne}/>
            <Route path="/need-for-drive/order-page/2" component={StepTwo}/>
            <Route path="/need-for-drive/order-page/3" component={StepThree}/>
            <Route path="4"/>
            </Switch>
            </OrderPage>
        </Route>
        </Switch>
        </>
    );
};
