import React from 'react';
import Header from '../../Header/Header';
import Total from './Total/Total';
import './style.scss';

export default class OrderPage extends React.Component {
  render() {
    return (
    <>
        <Header classNames="header order-page_header" />
      <div className="order-page">
      <div className="order-page__nav">
          <input
            type="button"
            className="order-page__nav__btn"
            value="Местоположение"
          />
          <span>►</span>
          <input
            type="button"
            className="order-page__nav__btn"
            value="Модель"
             disabled
          />
          <span>►</span>
          <input
            type="button"
            className="order-page__nav__btn"
            value="Дополнительно"
            disabled
          />
          <span>►</span>
          <input
            type="button"
            className="order-page__nav__btn"
            value="Итого"
            disabled
          />
        </div>
        <div className="order-page__container">
          <section className="order-page__container__form">
            {this.props.children}
          </section>
          <section className="order-page__container__total">
            <Total />
          </section>
        </div>
      </div>
      </>
    );
  }
}
